.button-arrow{
  padding: 16px;
  background: #FFFFFF;
  border-radius: 2px;

  &__rotated{
    svg{
      transform: rotate(180deg);
    }
  }

  &:hover{
   svg path{
      stroke: #585CC6;
    }
  }

  &:disabled{
    background: #E0E0E0;
    svg path{
      stroke: #ADADAD;
    }
  }
}