@import 'src/app/styles/variables';

.add-comment {
  position: fixed;
  top: 0%;
  left: 0%;
  width: 100vw;
  height: 100vh;
  z-index: 100;
  opacity: 0;
  transition: opacity 200ms ease-in;
  background: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
  pointer-events: none;
  display: flex;
  justify-content: center;
  align-items: center;

  &__content {
    background: #FFFFFF;
    padding: 24px;
    border-radius: 2px;
    width: 676px;
    max-height: max-content;

    @media (max-width: $md3) {
      padding: 16px;
      width: calc(100% - 30px);
      margin: 0 auto;

      input, textarea{
        width: 100%;
      }

      .button-purple{
        width: 100%;
      }
    }

    .textarea-block {
      margin-top: 32px;
    }

  }

  &_active {
    opacity: 1;
    pointer-events: auto;
  }

  &__header, &__name-file, &__submit {
    display: flex;
  }

  &__header {
    justify-content: space-between;
    margin-bottom: 20px;
    align-items: center;
    img{
      width: 20px;
      height: 20px;
    }
  }

  &__name-file {
    .input__wrapper {
      margin-top: 50px;
    }

    @media (max-width: $md3) {
      flex-direction: column;
      align-items: normal;
      .input__wrapper {
        margin-top: 20px;
        text-align: left;
      }
    }
  }

  &__file {
    align-items: center;
    margin-top: 12px;
    display: flex;
    background: #F5F5F5;
    border-radius: 2px;
    width: max-content;
    max-width: 500px;
    padding: 14px;

    @media (max-width: $md3) {
      margin-top: 16px;
      padding: 14px 12px;
      width: 100%;
      max-width: 100%;
      justify-content: space-between;
    }

    button {
      margin-left: 20px;

      &:hover svg path {
        opacity: 1;
      }

      @media (max-width: $md3) {
          margin-left: 0;
      }
    }

    &__name-load {
      width: 100%;
      max-width: 300px;
      margin-left: 12px;
      margin-right: 20px;

      p {
        font-family: 'Gilroy', sans-serif;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 130%;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        @media (max-width: $md3) {
          font-size: 14px;
          line-height: 130%;
        }
      }

      &_error {
        color: #EB5757;
      }

      &__stripe {
        margin-top: 4px;
        position: relative;

        &__bar {
          width: 110%;
          border: 4px solid #C2C4FF;
          border-radius: 4px;
        }

        &__load {
          position: absolute;
          top: 0;
          width: 110%;
          border: 4px solid #585CC6;
          border-radius: 4px;

          &_error {
            width: 110%;
            border: 4px solid #EB5757;
            border-radius: 4px;
          }
        }
      }
    }
  }

  &__submit {
    margin-top: 40px;
    align-items: center;

    @media (max-width: $md3) {
      flex-direction: column;
      align-items: flex-start;

      button{
        order: 2;
      }
    }

    &__info {
      margin-left: 16px;
      align-items: center;

      span {
        margin-left: 8px;
        color: #8A8A8A;
      }

      @media (max-width: $md3) {
        order: 1;
        margin-left: 0;
        margin-bottom: 12px;
        font-size: 10px;
        line-height: 12px;

        img{
          width: 8px;
          height: 8px;
        }
      }
    }
  }
}

.input__wrapper {
  width: 100%;
  position: relative;
  text-align: center;
}

.input__file {
  opacity: 0;
  visibility: hidden;
  position: absolute;
}

.input__file-button {
  cursor: pointer;
}